:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.is-hidden {
  display: none;
}

.gallery {
  max-width: 1140px;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 120px));
  grid-auto-rows: 150px;
  justify-content: center;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 120px;
  width: 240px;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.search-form {
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: block;
}

.info-item {
  color: #230d4d;
  flex-direction: column;
  margin-right: 20px;
  font-size: 8px;
  display: flex;
}

.info {
  justify-content: center;
  display: flex;
}

.load-more {
  margin: 30px auto;
}

.form-wrap {
  background-color: #00f;
  justify-content: center;
  display: flex;
}

.btn-wrap {
  justify-content: center;
  margin-bottom: 50px;
  display: flex;
}

